
.Detailshead-head {
  padding: 0em 2em 0 2em;
  background: #fff;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9;
  border-bottom: 1px solid #f1f1f1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.Detailshead-head div {
    font-size: 15px;
    position: relative;
    width: 1.6rem;
    -webkit-box-flex: 1;
    text-align: center;
    margin: 0 0.3em;
    padding: 0.65em 0;
    color: #605F5F;
    line-height: 0.37rem;
}
.Detailsheadbordercolor {
  border-bottom: 4px solid #ff0000;
}



