
.cart-div2 {
  background: url(//img.sanfu.com/img/index_boy/cart.png) center 5em no-repeat;
  background-size: 5.5em;
  text-align: center;
  width: 10em;
  margin: 0 auto;
  height: 11em;
}
.cart-div-a {
  height: 2em;
  line-height: 2em;
  background: #F51B1B;
  display: block;
  padding: 0em 1em;
  border: 0em solid #aaa;
  border-radius: 0em;
  width: 8em;
  margin: 1em auto;
  TEXT-DECORATION: none;
}
.cart-div-a-a {
  display: block;
  text-align: center;
}
.cart-div1 {
  width: 100%;
  height: 100%;
  position: relative;
}
.cart-div3-div {
  border-radius: 0em;
  border-top: dotted 0px #878787;
  padding: 0.5em;
  margin-bottom: 0.5em;
  background: #fff;
}
.cart-div3-yes {
  margin-top: 0.5em;
  width: 1.8em;
  background-size: 1.8em;
}
.cart-div3-yes-input {
  position: absolute;
  top: 4px;
  right: 20px;
  display: inline-block;
  width: 28px;
  height: 26px;
  border: 0;
  outline: 0 !important;
  background-color: transparent;
  -webkit-appearance: none;
}
.cart-div-img {
  width: 3.5em;
  border: 1px solid #ccc;
  margin: 0 0.5em;
  border-radius: 0.5em;
}
.Details-position-div-number1 {
  background: #D7D7D7;
  float: left;
  padding: 0.4em 0.8em;
  border: 1px solid #AFAFAF;
  border-radius: 0.5em 0 0 0.5em;
  display: inline-block;
}
.Details-position-div-number2 {
  float: left;
  padding: 0.4em 0.8em;
  border: 1px solid #AFAFAF;
  display: inline-block;
  border-left: 0;
  border-right: 0;
}
.Details-position-div-number3 {
  background: #D7D7D7;
  float: left;
  padding: 0.4em 0.8em;
  border: 1px solid #AFAFAF;
  border-radius: 0 0.5em 0.5em 0;
  display: inline-block;
}
.cart-div3 {
  font-size: 0.24rem;
  text-align: center;
}
.cart-div-img-name {
  width: 4.9rem;
}
.cart-div-img-name div {
    float: left;
}
.cart-number2 {
  float: right;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  color: #ccc;
}



