
.position {
  position: absolute;
  height: 680px;
  width: 100%;
  box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  z-index: 1;
  background: #fff;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  left: 0;
  z-index: 4;
}
.position-head {
  border-bottom: 1px solid #ddd;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 29px;
  padding: 10px 0px 9px 7px;
}
.position-search {
  width: 30px;
  height: 29px;
  text-align: center;
  line-height: 29px;
  background: #f7f7f7;
  border-radius: 10px 0px 0px 10px;
}
.search-text {
  width: 290px;
  height: 29px;
  border: none;
  border-radius: 0px 10px 10px 0px;
  background: #f7f7f7;
}
.search-button {
  margin-left: 10px;
  width: 46px;
  height: 29px;
  border-radius: 10px;
  background: #007aff;
  border: none;
  color: #fff;
  text-align: center;
  line-height: 29px;
}
.Recently {
  width: 100%;
  box-sizing: border-box;
  border-bottom: 1px solid #ddd;
  padding: 13px 0px 0px 0px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.Recently-div {
  margin: 0px 13px 0px 7px;
  height: 17px;
}
.Recently-div span {
    line-height: 17px;
    font-size: 13px;
    float: left;
    display: block;
}
.Recently-div i {
    width: 14px;
    height: 17px;
    text-align: center;
    line-height: 17px;
    display: block;
    float: right;
    color: #8f8f94;
}
.hot {
  margin-top: 13px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.hot-div {
  height: 20px;
  padding: 0px 10px 0px 7px;
}
.hot-left {
  display: block;
  float: left;
  line-height: 20px;
  font-size: 14px;
}
.hot-right {
  display: block;
  float: right;
  line-height: 20px;
  font-size: 14px;
  color: #4eb6be;
}
.hot-ul,
.Recently-ul {
  padding: 17px 41px 0px 8px;
  box-sizing: border-box;
}
.hot-ul li,
  .Recently-ul li {
    margin: 0px 6px 12px 0px;
    padding: 6px 8px 6px 8px;
    border-radius: 6px;
    border: 1px solid #dddddd;
    color: #9fa09f;
    float: left;
    font-size: 12px;
    text-align: center;
    line-height: 11px;
    background: #f7f7f7;
}
.search-a {
  position: absolute;
  width: 46px;
  height: 29px;
  right: 0.14rem;
  top: .15rem;
  display: none;
}



