
.cloth {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 999999;
  left: 0px;
  top: 0px;
}
.head {
  width: 100%;
  height: 44px;
  background: #f3f3f3;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.menu {
  width: 10%;
  height: 44px;
  text-align: center;
  line-height: 44px;
}
.input {
  width: 70%;
  height: 31px;
  border-radius: 0px 15px 15px 0px;
  border: none;
  background: #fff;
  color: #D8D8D8;
  font-size: 1em;
  line-height: 31px;
}
.search {
  width: 70%;
  height: 31px;
  margin-top: 7px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.glass {
  width: 20%;
  height: 31px;
  text-align: center;
  line-height: 31px;
  background: #fff;
  border-radius: 15px 0px 0px 15px;
}
.fa-search {
  color: #e3e3e3;
}
.gender {
  margin-top: 7px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.neuter,
.stroke {
  width: 28px;
  height: 28px;
  text-align: center;
  line-height: 28px;
  color: #fff;
}
.neuter {
  background: #ccc;
}
.stroke {
  background: #d63838;
  margin-left: 3px;
}
.angle {
  width: 21px;
  height: 29px;
  text-align: center;
  line-height: 29px;
}



