
.ficapos {
  width: 100%;
  position: absolute;
  top: 0;
  background: #fff;
  z-index: 3;
  padding-bottom: 40px;
}
.ficaposp {
  font-size: 15px;
  text-align: left;
  height: 30px;
  padding-left: 15px;
  background-color: white;
  padding-top: 15px;
  padding-bottom: 4px;
  float: left;
}
.ficaposp2 {
  font-size: 15px;
  text-align: left;
  height: 30px;
  padding-right: 15px;
  background-color: white;
  padding-top: 15px;
  padding-bottom: 4px;
  float: right;
  color: #808080;
}
.ficapospdiv {
  box-sizing: border-box;
  font-size: 0.6em;
  width: 50%;
  height: 33px;
  padding-left: 15px;
  background-color: #f7f7f7;
  padding-top: 8px;
  border-right: solid;
  border-right-color: white;
  border-right-width: 1px;
  border-bottom: 1px solid white;
}



