
.tail {
  width: 100%;
  height: 41px;
  position: fixed;
  bottom: 0px;
  background: #f7f7f7;
  box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-top: 1px solid #ccc;
  left: 0px;
}
.page {
  width: 100%;
  height: 100%;
  text-align: center;
  line-height: 41px;
  box-sizing: border-box;
  padding: 10px 0px 0px 23px;
  border-right: 1px solid #dddddd;
}
.page-i {
  width: 20px;
  height: 20px;
  background: url(../../assets/images/page.jpg) no-repeat;
  display: block;
}
.bars, .bars-2, .bars-3 {
  width: 14px;
  height: 18px;
  line-height: 18px;
  text-align: center;
}
.classification, .activity, .my {
  height: 100%;
  padding: 10px 0px 0px 33px;
  box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-right: 1px solid #ddd;
}
.classification i, .activity i, .my i {
    color: #929292;
}
.activity, .my {
  width: 28%;
}
.baspan, .baspan-2, .baspan-3 {
  width: 38px;
  height: 18px;
  display: block;
  line-height: 18px;
  color: #929292;
  margin-left: 3px;
}
.activity-pos {
  width: 30%;
  position: absolute;
  top: -143px;
  left: 40%;
  background: #f7f7f7;
  border-radius: 10px;
  padding-left: 13px;
  display: none;
}
.activity-pos-div {
  line-height: 42px;
  font-size: 16px;
}
.activity-pos-div a {
    display: block;
    border-bottom: 1px #e0dfe2 solid;
}
.activity-pos-div a:nth-child(3) {
    border: none;
}
.activity-pos-div-pos {
  position: absolute;
  width: 0px;
  height: 0px;
  border: 13px #f7f7f7 solid;
  left: 66px;
  border-color: #f7f7f7 transparent transparent transparent;
}
.activity-pos2 {
  width: 30%;
  position: absolute;
  top: -143px;
  left: 66%;
  background: #f7f7f7;
  border-radius: 10px;
  padding-left: 13px;
  display: none;
}



