
@charset "UTF-8";
@import url(assets/css/font-awesome.min.css);
@import url(assets/css/swiper.min.css);
html {
  width: 100%;
  height: 100%;
}
.app {
  height: 100%;
}
[v-cloak] {
  display: none;
}
body,
p,
h1,
h2,
h3,
h4,
div,
ul,
li,
dl,
dt,
dd,
form,
input,
select,
option,
textarea,
a,
img,
em,
i,
strong,
b {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
input,
select,
option,
textarea {
  font-family: "微软雅黑";
  outline: none;
  resize: none;
}
a {
  text-decoration: none;
  color: #000;
}
a:hover {
  color: #000;
}
a:visited {
  color: #000;
}
img {
  vertical-align: top;
  border: 0 none;
  width: 100%;
  height: 100%;
  background: url(./assets/images/loading.gif) no-repeat;
  background-position-x: 50%;
  background-position-y: 50%;
  background-size: 10%;
}
html {
  font-size: 625%;
  box-sizing: border-box;
}
body {
  max-width: 750px;
  min-width: 320px;
  margin: 0 auto;
  font-family: "微软雅黑";
  background: #f7f7f7;
  font-size: 18px;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}
.clearfix:after {
  content: ".";
  display: block;
  visibility: hidden;
  clear: both;
  height: 0;
  font-size: 0;
}



