
.cationfixed {
  height: 2.5em;
  width: 2.5em;
  border-radius: 0.5em;
  line-height: 1.3em;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  bottom: 4em;
  right: 0.4em;
  background: rgba(133, 133, 133, 0.65);
  text-align: center;
  z-index: 8;
}
.cationfixed p {
    margin: 0.31rem 0px 0px 0.1rem;
    width: .08rem;
    height: .08rem;
    border-radius: 1px;
    background: #fff;
}
.cationfixedbu {
  position: fixed;
  z-index: 4;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.3);
  display: none;
}
.cationfixeddiv {
  width: 86px;
  position: absolute;
  top: -144px;
  left: -44px;
  display: none;
}
.cationfixeddiv div {
    background-color: #fff;
}
.cationfixedfen {
  border-bottom: 1px solid #f1f1f1;
  position: relative;
  z-index: 9;
  border-radius: 5px;
}
.cationfixedfen a span {
    display: block;
    line-height: 29px;
    border-bottom: 1px solid #f1f1f1;
    font-size: 15px;
}
.cationfixedhaed {
  width: 0px;
  height: 0px;
  border: 13px solid;
  position: absolute;
  z-index: 9;
  border-color: #fff transparent transparent transparent;
  background-color: transparent !important;
  right: 10px;
  bottom: -24px;
}



