
.Commentdetails-div {
  background: #fff;
  padding-top: 10px;
  padding-left: 10px;
  padding-bottom: 10px;
  font-size: 0.24rem;
}
.Commentdetails-div-div1 {
  margin-right: 6px;
  border-radius: 0.5em;
  border: 1px #ff0000 solid;
  color: #ff0000;
  padding: 0.15em 0.6em;
  float: left;
}
.commentdetails-background {
  background-color: #ff0000;
  color: #fff !important;
}
.Commentdetails-div-div2 {
  float: left;
  margin-right: 6px;
  border-radius: 0.5em;
  border: 1px #ff0000 solid;
  color: #ff0000;
  padding: 0.15em 0.6em;
}
.Commentdetails-div-div3 {
  float: right;
  margin-right: 0.3em;
  color: #ff0000;
  font-size: 0.9em;
  margin-top: 0.3em;
}
.Commentdetails-div2 {
  background: #fff;
  font-size: 0.8em;
  padding: 0.5em;
  margin: 0.1em 0;
  margin-bottom: 0.4em;
}
.Commentdetails-div2-div1 {
  color: #404040;
  padding: 0.3em;
  border-bottom: 1px solid #f1f1f1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 0.27rem;
}
.Commentdetails-div2-div1 img {
    height: 100%;
    width: auto;
}
.Commentdetails-div2-div2 {
  color: #b4b4b4;
  font-size: 0.7em;
}
.Commentdetails-div-id {
  color: #858688;
  background: #f2f2f3;
  padding: 0.1em 0.4em;
  border-radius: 0.6em;
  font-size: 0.7em;
}
.commentdetails-img {
  text-align: center;
  padding: 1em;
  background: #f3f3f3;
  color: #ccc;
  display: none;
}



