
.Detailshead-list {
  background: #fff;
  text-align: center;
  height: 3em;
  line-height: 3em;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 9;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.Detailshead-list-div1 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-right: .03rem;
  height: 100%;
}
.Detailshead-list-div1 div {
    padding-top: .15rem;
    margin-left: .1rem;
    width: .78rem;
    line-height: 0;
}
.Detailsheadsize {
  font-size: 12px;
  width: 100%;
  margin-top: .1rem;
  margin-left: 0.01rem !important;
}
.Detailshead-list-div2 {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.Detailshead-list-div2 div {
    font-size: 13px;
    color: #fff;
    padding: 0.03rem  0.5rem 0 .45rem;
    width: 50%;
    text-align: center;
}
.Detailshead-list-div2 a {
    color: #fff;
}



