
.headplugin-color {
  color: #f00;
}
.headpluin-dabian {
  background: #fff;
  position: fixed;
  width: 100%;
  z-index: 2;
}
.headpluin-head {
  width: 11%;
  text-align: center;
  line-height: .69rem;
}
.headpluin-head-div {
  background: #f3f3f3;
  width: 26px;
  color: #dbdbdb;
  border: 1px #dbdbdb solid;
  border-right: none;
  border-radius: 0.1rem 0 0 0.1rem;
  padding: 0.05rem 0 0.08rem 0.08rem;
  height: 0.3rem;
  line-height: 0.3rem;
}
.headpluin-head-div-2 {
  background: #f3f3f3;
  width: 4.4rem;
  border: 1px #dbdbdb solid;
  border-radius: 0 0.1rem  0.1rem 0;
  border-left: none;
  height: 0.43rem;
}
.headpluin-fenlei {
  width: 0.3rem;
  height: 0.3rem;
  margin: .23rem 0 0 .30rem;
  background: url("../../assets/images/fenlei.jpg") no-repeat;
}
.headplugin {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-bottom: 0.3rem;
}
.headplugin div {
    margin: 0.2rem 0.39rem 0px 0.51rem;
    font-size: 13px;
    line-height: 17px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.headplugin div div {
      margin: 0;
}
.headplugini1, .headplugini {
  padding-top: 0.03rem;
  padding-left: 0.06rem;
}
.headplugin-div-1, .headplugin-div-2 {
  height: 0.61rem;
  font-size: 13px;
  line-height: 0.61rem;
  border-bottom: 1px solid #f1f1f1;
  padding-left: 0.2rem;
}
.headpluin-hide {
  padding-left: 0.14rem;
  display: none;
}
.headpluinnone {
  display: none;
  height: 0.98rem;
  line-height: 0.98rem;
  text-align: center;
  font-size: 14px;
}



